import { NextResponse } from 'next/server';
import pool from '../../../lib/db'; // ‡πÉ‡∏ä‡πâ Path ‡πÄ‡∏î‡∏¥‡∏°‡∏ó‡∏µ‡πà‡∏Ñ‡∏∏‡∏ì‡∏ö‡∏≠‡∏Å‡∏ß‡πà‡∏≤‡∏£‡∏±‡∏ô‡∏ú‡πà‡∏≤‡∏ô

export async function GET(request) {
  try {
    // 1. ‡∏î‡∏∂‡∏á‡∏£‡∏≤‡∏¢‡∏ä‡∏∑‡πà‡∏≠‡∏Ñ‡∏π‡πà‡∏Ñ‡πâ‡∏≤
    const [suppliers] = await pool.query(`
      SELECT id, code, name, credit_term, contact_name, address 
      FROM suppliers 
      ORDER BY id DESC
    `);

    // 2. ‡∏î‡∏∂‡∏á‡∏£‡∏≤‡∏¢‡∏ä‡∏∑‡πà‡∏≠‡∏™‡∏¥‡∏ô‡∏Ñ‡πâ‡∏≤ (‡πÅ‡∏Å‡πâ‡πÑ‡∏Ç‡πÉ‡∏´‡∏°‡πà‡πÉ‡∏´‡πâ‡∏ï‡∏£‡∏á‡∏Å‡∏±‡∏ö Database ‡∏à‡∏£‡∏¥‡∏á‡∏Ç‡∏≠‡∏á‡∏Ñ‡∏∏‡∏ì)
    // - ‡πÄ‡∏õ‡∏•‡∏µ‡πà‡∏¢‡∏ô product_code AS code (‡πÄ‡∏û‡∏∑‡πà‡∏≠‡πÉ‡∏´‡πâ‡∏´‡∏ô‡πâ‡∏≤‡∏ö‡πâ‡∏≤‡∏ô‡πÉ‡∏ä‡πâ .code ‡πÑ‡∏î‡πâ‡πÄ‡∏´‡∏°‡∏∑‡∏≠‡∏ô‡πÄ‡∏î‡∏¥‡∏°)
    // - ‡πÄ‡∏õ‡∏•‡∏µ‡πà‡∏¢‡∏ô price AS cost_price (‡πÄ‡∏û‡∏∑‡πà‡∏≠‡πÉ‡∏´‡πâ‡∏´‡∏ô‡πâ‡∏≤‡∏ö‡πâ‡∏≤‡∏ô‡πÉ‡∏ä‡πâ .cost_price ‡πÑ‡∏î‡πâ‡πÄ‡∏´‡∏°‡∏∑‡∏≠‡∏ô‡πÄ‡∏î‡∏¥‡∏°)
    const [products] = await pool.query(`
        SELECT 
            id, 
            product_code AS code, 
            name, 
            unit, 
            price AS cost_price 
        FROM products 
        ORDER BY name ASC
    `);

    // ‡∏™‡πà‡∏á‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡∏Å‡∏•‡∏±‡∏ö‡πÑ‡∏õ‡πÉ‡∏´‡πâ‡∏´‡∏ô‡πâ‡∏≤‡∏ö‡πâ‡∏≤‡∏ô
    return NextResponse.json({
      suppliers,
      products
    });

  } catch (error) {
    console.error("API Error:", error.message); // ‡∏î‡∏π Error ‡πÑ‡∏î‡πâ‡∏ó‡∏µ‡πà Terminal
    return NextResponse.json({ error: error.message }, { status: 500 });
  }
}